.alerta{
    padding: .5rem;
    text-align: center;
    color: white;
    font-weight: bold;
    text-transform: uppercase;
    margin: 1rem 0;
    &.error{
        background-color: $red;
    }
    &.pass{
        background-color: green;
    }
}

table.propiedades{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    thead{
        background-color: $verde;

        th{
            color: $blanco;
            padding: 2rem;
        }
    }
    .imagen-tabla{
        width: 20rem;
        margin: 0 auto;
    }
    tbody td{
        text-align: center;
        vertical-align: middle;
    }
}
.imagen_pequeña{
    width: 20rem ;
}